import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import "./App.css";

import NavMenu from "./components/IconsMenu";
import Home from "./pages/Home/Home";
import AboutMePage from "./pages/AboutMe/AboutMe";
import WorkExperience from "./pages/Experience/Experience";
import EducationPage from "./pages/Education/Education";
import NotFound from "../src/components/notFound";

const items = [
  {
    title: "Home",
    link: "/home",
  },
  {
    title: "About me",
    link: "/aboutme",
  },
  {
    title: "Work experience",
    link: "/work_experience",
  },
  {
    title: "Education",
    link: "/education",
  },
];

function App() {
  return (
    <React.Fragment>
      <NavMenu items={items} />
      <main className="container">
        <Switch>
          <Route path="/portfolio-v2/home" component={Home} />
          <Route path="/portfolio-v2/aboutme" component={AboutMePage} />
          <Route
            path="/portfolio-v2/work_experience"
            component={WorkExperience}
          />
          <Route path="/portfolio-v2/education" component={EducationPage} />
          <Route path="/portfolio-v2/not-found" component={NotFound}></Route>
          <Redirect from="/" exact to="/portfolio-v2/home" />
          <Redirect from="/portfolio-v2" exact to="/portfolio-v2/home" />
          <Redirect to="/portfolio-v2/not-found" />
        </Switch>
      </main>
    </React.Fragment>
  );
}

export default App;
